.modal {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;

  background-color: rgba(26, 41, 49, 0.6);

  display: none;

  width: 100%;
  height: 100%;
}

.modal__content {
  max-width: 490px;
  height: 605px;

  background-color: $skyBlue;

  padding-top: 33px;
  padding-bottom: 3px;
  padding-right: 40px;
  padding-left: 40px;

  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);

  overflow: auto;
}

.modal--open {
  display: block;
}

.modal__input-container input,
.modal__textarea-container textarea {
  background-color: #19baff;
  border: none;

  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 23px;
  padding-right: 23px;

  width: 100%;

  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
}

.modal__input-container {
  width: 100%;

  margin-bottom: 30px;
}

.modal__input-container--name,
.modal__input-container--tel {
  height: 52px;
}

.modal__input-container input::placeholder,
.modal__textarea-container textarea::placeholder {
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
}

.modal__button--submit {
  padding-top: 15px;
  padding-bottom: 15px;
  margin-bottom: 30px;

  width: 100%;
}

.modal__button--close {
  border: none;

  font-size: 0;

  width: 16px;
  height: 16px;

  position: absolute;
  top: 20px;
  right: 20px;

  background-image: url("data:image/svg+xml,%3Csvg width='17' height='17' viewBox='0 0 17 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M0.146522 0.853553C-0.04874 0.658291 -0.04874 0.341709 0.146522 0.146447C0.341784 -0.0488155 0.658367 -0.0488156 0.853629 0.146447L8.27821 7.57103L15.7028 0.14645C15.8981 -0.0488124 16.2146 -0.0488125 16.4099 0.14645C16.6052 0.341712 16.6052 0.658294 16.4099 0.853556L8.98532 8.27814L16.41 15.7028C16.6052 15.8981 16.6052 16.2146 16.41 16.4099C16.2147 16.6052 15.8981 16.6052 15.7029 16.4099L8.27821 8.98525L0.853553 16.4099C0.658291 16.6052 0.341709 16.6052 0.146447 16.4099C-0.0488155 16.2146 -0.0488156 15.8981 0.146447 15.7028L7.57111 8.27814L0.146522 0.853553Z' fill='white'/%3E%3C/svg%3E%0A");
  background-position: center;
  background-repeat: no-repeat;
}

.modal__inputs-container {
  width: 100%;

  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.modal input:hover,
.modal textarea:hover,
.modal input[type="checkbox"]:hover + label,
.modal__button--close:hover {
  opacity: 0.6;
}

.modal__button--close:active {
  opacity: 0.3;
}

.modal input:focus,
.modal textarea:focus,
.modal input[type="checkbox"]:active + label {
  opacity: 1;

  outline: none;
}

.modal input[type="checkbox"]:focus + label {
  opacity: 0.6;
}

.modal__button--close:active:focus {
  outline: none;
}

.modal label {
  display: block;

  color: #ffffff;

  opacity: 0.8;

  padding-left: 48px;
  margin-top: 2px;

  position: relative;
}

.modal label:hover {
  cursor: pointer;
}

.modal input[type="checkbox"] + label::before {
  content: "";

  position: absolute;
  top: 5px;
  left: 0;

  width: 16px;
  height: 16px;

  border: 1px solid #ffffff;
}

.modal input[type="checkbox"]:checked + label::before {
  background-image: url("data:image/svg+xml,%3Csvg width='9' height='6' viewBox='0 0 9 6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.5 2L3.5 5L8 0.5' stroke='white'/%3E%3C/svg%3E%0A");
  background-position: center;
  background-repeat: no-repeat;
}

.modal textarea {
  resize: none;

  min-height: 104px;

  padding-top: 10px;
  padding-bottom: 10px;

  overflow: auto;
}

.modal h2 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 28px;
  line-height: 140.62%;

  color: #ffffff;

  margin: 0;
  padding: 0;
  margin-bottom: 10px;
}

.modal p {
  line-height: 22px;
  color: #ffffff;

  margin: 0;
  padding: 0;
  padding-right: 10px;
  margin-bottom: 30px;
}

.modal__textarea-container {
  margin-bottom: 20px;

  width: 100%;

  height: 104px;
}
