.features__list {
  @include listReset;

  display: flex;
  flex-wrap: wrap;

  max-width: 1170px;
  margin: 0 auto;
}

.features__item h3 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 34px;

  color: #00b2ff;

  margin: 0;
  margin-bottom: 15px;
  padding: 0;
}

.features__item p {
  line-height: 22px;

  color: #666666;

  margin: 0;
  padding: 0;
  padding-right: 25px;
}

.features__item {
  width: 48.7%;
  min-height: 299px;

  border: 1px solid #ebebeb;
  border-left: 3px solid #00b2ff;

  padding-top: 40px;
  padding-left: 45px;

  margin-bottom: 30px;
  margin-right: 2.6%;

  position: relative;

  background-color: #ffffff;

  &:nth-child(2n) {
    margin-right: 0;
  }
}

.features__icon {
  margin-bottom: 15px;

  fill: $skyBlue;
}

.features__item:hover {
  background-color: $skyBlue;
}

.features__item:hover p,
.features__item:hover h3 {
  color: #ffffff;
}

.features__item:hover .features__icon {
  fill: #ffffff;
}

@media (max-width: $desktop-res) {
  .features__icon {
    margin-bottom: 5px;
  }

  .features__item {
    width: 48%;
    min-height: 306px;

    margin-right: 4%;
    padding-top: 25px;
    padding-left: 23px;

    &:nth-child(2n) {
      margin-right: 0;
    }
  }

  .features__item--suppliers,
  .features__item--confidential {
    min-height: 232px;
  }

  .features__list {
    width: 95.5%;
  }

  .features__item h3 {
    margin-bottom: 5px;
  }
}

@media (max-width: $tablet-res) {
  .features__icon {
    margin-bottom: 8px;
  }

  .features__list {
    width: 98%;
  }

  .features__item h3 {
    line-height: 140.62%;
    font-size: 20px;

    margin-bottom: 10px;
    padding-right: 10px;
  }

  /* .features__item--suppliers h3 {
    margin-bottom: 0px;
  } */

  .features__item p {
    padding-right: 3px;
  }

  .features__item--timescale p {
    padding-right: 2px;
  }

  .features__item {
    width: 100%;
    min-height: 310px;

    margin-right: 0;
    margin-bottom: 20px;
    padding-top: 20px;
    padding-right: 15px;
  }

  .features__item--suppliers {
    min-height: 232px;
  }

  .features__item--confidential {
    min-height: 244px;
  }
}
