.about {
  max-width: 1170px;
  margin: 0 auto;
  padding-bottom: 50px;

  padding-left: 180px;

  background-image: url("../img/background-2005-desktop@1x.png");

  background-position: -3% -1%;
  background-repeat: no-repeat;
}

.webp .about {
  background-image: url("../img/background-2005-desktop@1x.webp");
}

@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .about {
    background-image: url("../img/background-2005-desktop@2x.png");
    background-size: 220px 317px;
  }

  .webp .about {
    background-image: url("../img/background-2005-desktop@2x.webp");
  }
}

.about__button {
  display: block;

  color: $skyBlue;

  font-size: 14px;
  line-height: 16px;

  border: 1px solid $skyBlue;

  min-width: 180px;

  padding-top: 15px;
  padding-bottom: 15px;
  margin-bottom: 70px;
}

.about__button:hover {
  color: rgba(0, 178, 255, 0.6);
}

.about__button:active {
  color: rgba(0, 178, 255, 0.3);
}

.about h2 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 36px;
  line-height: 51px;

  color: #666666;

  margin: 0;
  margin-bottom: 20px;
  padding: 0;
}

.about__text {
  margin: 0;
  margin-bottom: 20px;
  padding: 0;

  line-height: 22px;
}

.about__text-bottom-desktop {
  margin-bottom: 37px;
}

.about__text-bottom-tablet {
  display: none;
}

@media (max-width: $desktop-res) {
  .about {
    padding-left: 200px;

    background-image: url("../img/background-2005-tablet@1x.png");
  }

  .webp .about {
    background-image: url("../img/background-2005-tablet@1x.webp");
  }

  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .about {
      background-image: url("../img/background-2005-tablet@2x.png");
      background-size: 244px 362px;
    }

    .webp .about {
      background-image: url("../img/background-2005-tablet@2x.webp");
    }
  }

  .about__button {
    margin-bottom: 30px;
  }

  .about h2 {
    font-size: 32px;
    line-height: 140.62%;

    margin-bottom: 27px;
  }

  .about__text-bottom-tablet {
    display: block;

    margin-bottom: 40px;
  }

  .about__text-bottom-desktop {
    display: none;
  }
}

@media (max-width: $tablet-res) {
  .about {
    padding-left: 10px;

    background: none;
  }

  .webp .about {
    background: none;
  }

  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .about {
      background: none;
    }

    .webp .about {
      background: none;
    }
  }

  .about__button {
    margin-bottom: 10px;
  }

  .about h2 {
    font-size: 28px;

    margin-bottom: 23px;
  }

  .about__text:first-of-type {
    margin-bottom: 40px;
  }

  .about__text:last-of-type {
    margin-bottom: 27px;
  }
}
